<template>
  <div id="process-standard">
    <!--  -->
    <el-card v-if="!process_input_data.length > 0" class="warning" shadow="hover">
      <el-alert title="" type="warning" show-icon :closable="false">
      </el-alert>
    </el-card>
    <!--  -->
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-select v-model="process_setting.standard[0].state" placeholder="">
            <el-option v-for="state in process_standard_states" :key="state.value" :label="state.label"
              :value="state.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-select v-model="process_setting.standard[0].method" placeholder="">
            <el-option v-for="method in process_standard_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <!--  -->
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          
        </b>
      </div>
      
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'ProcessStandard',
  data() {
    return {
      // 
      process_standard_states: [
        {
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }
      ],
      // 
      process_standard_methods: [
        {
          value: 1,
          label: 'Min-max'
        }, {
          value: 2,
          label: 'Z-score'
        }, {
          value: 3,
          label: 'MaxAbs'
        }, {
          value: 4,
          label: 'RobustScaler'
        }, {
          value: 5,
          label: ''
        }
      ],
    };
  },
  computed: {
    process_input_data: { // 
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
    process_setting: { // 
      get() {
        return this.$store.state.process_setting;
      },
      set(newVal) {
        this.$store.commit('set_process_setting', newVal);
      }
    },
  },
  watch: {
    // process_setting
    process_setting: {
      handler(newVal, oldVal) {
        this.process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
  },
  mounted() {
  }
};
</script>

<style lang="scss" scoped>
#process-standard {

  // card
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>