<template>
  <div id="process-input">
    <el-card class="setting" shadow="hover">
      <div class="setting-method" style="margin-right: 20px;">
        <span></span>
        <el-select v-model="process_input_method" placeholder="">
          <el-option v-for="method in process_input_methods" :key="method.value" :label="method.label"
            :value="method.value">
          </el-option>
        </el-select>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <el-table :data="process_info_form" stripe>
        <el-table-column v-for="column in process_form_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
    <el-card v-if="process_input_method == 1" class="form-example" shadow="hover">
      <div slot="header">
        <b>
          
        </b>
      </div>
      <el-table :data="process_example_form" style="width: 100%" stripe>
        <el-table-column v-for="column in process_form_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
    <el-card v-if="process_input_method == 1" class="form-upload" shadow="hover">
      <div slot="header">
        <el-upload class="upload-file" ref="upload" action="/" :show-file-list="false" :on-change="upload_excel"
          accept=".csv,.xlsx,.xls" :auto-upload="false">
          <el-button icon="el-icon-upload" size="small" type="primary">
            
          </el-button>
        </el-upload>
      </div>
      <el-table v-if="process_input_data.length > 0" :data="process_input_data" style="width: 100%" stripe>
        <el-table-column v-for="column in process_form_columns" :key="column" :prop="column" :label="column">
        </el-table-column>
      </el-table>
    </el-card>
    <el-card v-if="process_input_method == 2" class="form-online" shadow="hover">
      online
    </el-card>
  </div>
</template>

<script>
import * as XLSX from 'xlsx';

export default {
  name: 'ProcessInput',
  data() {
    return {
      // 
      process_input_methods: [{
        value: 1,
        label: ''
      }, {
        value: 2,
        label: ''
      }],
      // 
      process_form_columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR"],
      // 
      process_info_form: [
        { // 
          "SPO": "",
          "BMPO": "",
          "BFPO": "",
          "SPE": "",
          "BMPE": "",
          "BFPE": "",
          "ST": "",
          "BT": "",
          "WP": "",
          "FN": "",
          "SFR": "",
          "BFR": "",
        },
        { //
          "SPO": "",
          "BMPO": "",
          "BFPO": "",
          "SPE": "",
          "BMPE": "",
          "BFPE": "",
          "ST": "",
          "BT": "",
          "WP": "",
          "FN": "",
          "SFR": "",
          "BFR": "",
        }
      ],
      // 
      process_example_form: [
        {
          "SPO": "0.1",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        },
        {
          "SPO": "0.15",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        },
        {
          "SPO": "0.2",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        },
        {
          "SPO": "0.25",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        },
        {
          "SPO": "0.3",
          "BMPO": "0.078",
          "BFPO": "1",
          "SPE": "2.50E-13",
          "BMPE": "2.31E-15",
          "BFPE": "1.15E-10",
          "ST": "5.00E+01",
          "BT": "1.40E+02",
          "WP": "2.50E+02",
          "FN": "1.50E+02",
          "SFR": "5.00E-06",
          "BFR": "5.00E-06",
        }
      ]
    };
  },
  computed: {
    process_input_method: { // 
      get() {
        return this.$store.state.process_input_method;
      },
      set(newVal) {
        this.$store.commit('set_process_input_method', newVal);
      }
    },
    process_input_data: { // 
      get() {
        return this.$store.state.process_input_data;
      },
      set(newVal) {
        this.$store.commit('set_process_input_data', newVal);
      }
    },
  },
  methods: {
    // 
    upload_excel(file) {
      new Promise(function (resolve, reject) {
        const reader = new FileReader();
        reader.onload = function (e) {
          const data = e.target.result;
          this.wb = XLSX.read(data, {
            type: 'binary'
          });
          const result = [];
          this.wb.SheetNames.forEach((sheetName) => {
            result.push({
              sheetName: sheetName,
              columns: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName], { header: 1 })[0],
              sheet: XLSX.utils.sheet_to_json(this.wb.Sheets[sheetName]),
            });
          });
          resolve(result);
        };
        reader.readAsBinaryString(file.raw);
      }).then(tabJson => {
        if (tabJson && tabJson.length > 0) {
          // store
          // this.process_upload_sheetname = tabJson[0].sheetName; // sheetname
          // this.process_upload_columns = tabJson[0].columns; // 
          this.process_input_data = tabJson[0].sheet; //
        }
      });
    },
  }
};
</script>

<style lang="scss" scoped>
#process-input {

  // card
  &>* {
    margin-bottom: 20px;
  }
}
</style>
