<template>
  <div id="train-algorithm-lr">
    <el-card shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-input-number v-model="train_algorithm_lr_setting.test_size" :min="0.05" :max="0.3"
            :step="0.01"></el-input-number>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span>0</span>
          <el-select v-model="train_algorithm_lr_setting.paras[0].fit_intercept" placeholder="0">
            <el-option v-for="method in fit_intercept_state" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      111
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b></b>
      </div>
      
    </el-card>
    <el-card class="form-upload" shadow="hover">
      <div slot="header">
        <div class="model-tool">
          <el-button type="primary" @click="train_model"></el-button>
          <el-button type="primary" @click="save"></el-button>
        </div>
      </div>
      <div v-show="Object.keys(train_algorithm_lr_result).length > 0" class="model-result">
        <!--  -->
        <b>
          BSR
        </b>
        <el-table v-if="Object.keys(train_algorithm_lr_result).length > 0" :data="[train_algorithm_lr_result.bsr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <b>
          SBR
        </b>
        <el-table v-if="Object.keys(train_algorithm_lr_result).length > 0" :data="[train_algorithm_lr_result.sbr.error]"
          style="width: 100%;margin-bottom: 30px;" stripe>
          <el-table-column v-for="column in error_columns" :key="column" :prop="column" :label="column">
          </el-table-column>
        </el-table>
        <!--  -->
        <div id="lr-bsr-lineplot"></div>
        <div id="lr-sbr-lineplot"></div>
        <div id="lr-d-lineplot"></div>
      </div>
    </el-card>
  </div>
</template>

<script>
// import * as XLSX from 'xlsx';
import { train } from "@/api";
import Plotly from 'plotly.js-dist-min';


export default {
  name: 'TrainAlgorithmLr',
  data() {
    return {
      // 
      fit_intercept_state: [{
        value: "true",
        label: ''
      }, {
        value: "false",
        label: ''
      }],
      // 
      error_columns: ["MAE", "RMSE", "R2"],
    };
  },
  computed: {
    train_process_result: { // 
      get() {
        return this.$store.state.train_process_result;
      },
      set(newVal) {
        this.$store.commit('set_train_process_result', newVal);
      }
    },
    train_algorithm_lr_setting: { // 
      get() {
        return this.$store.state.train_algorithm_lr_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lr_setting', newVal);
      }
    },
    train_algorithm_lr_result: { // 
      get() {
        return this.$store.state.train_algorithm_lr_result;
      },
      set(newVal) {
        this.$store.commit('set_train_algorithm_lr_result', newVal);
      }
    },
    train_save: { // 
      get() {
        return this.$store.state.train_save;
      },
      set(newVal) {
        this.$store.commit('set_train_save', newVal);
      }
    },
  },
  watch: {
    // train_algorithm_lr_setting
    train_algorithm_lr_setting: {
      handler(newVal, oldVal) {
        this.train_algorithm_lr_setting = newVal;
      },
      deep: true,
      immediate: true
    },
    // // train_algorithm_lr_setting
    // train_algorithm_lr_setting: {
    //   handler(newVal, oldVal) {
    //     this.train_algorithm_lr_setting = newVal;
    //   },
    //   deep: true,
    //   immediate: true
    // },
  },
  methods: {
    // 
    train_model() {
      // 
      if (this.train_process_result.length > 0) {
        // 
        this.train_algorithm_lr_setting.data = this.train_process_result;
        train(this.train_algorithm_lr_setting).then(result => {
          this.train_algorithm_lr_result = result;
          // 
          this.draw_lineplot_all();
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },
    draw_lineplot_all() {
      // 
      this.draw_lineplot("lr-bsr-lineplot", "bsr");
      this.draw_lineplot("lr-sbr-lineplot", "sbr");
      this.draw_lineplot("lr-d-lineplot", "d");
    },
    // 
    save() {
      // 
      if (Object.keys(this.train_algorithm_lr_result).length > 0) {
        const train_algorithm_lr_setting = this.train_algorithm_lr_setting;
        const train_algorithm_lr_result = this.train_algorithm_lr_result;
        this.train_save = {
          train_algorithm_setting: train_algorithm_lr_setting,
          train_algorithm_result: train_algorithm_lr_result
        };
        // 
        this.$message({
          message: '',
          type: 'success'
        });
      } else {
        this.$message({
          showClose: true,
          message: '',
          type: 'error'
        });
      }
    },
    // 
    draw_lineplot(id, tag) {
      // 
      let title = "";
      if (tag == "bsr") {
        title = "BSR";
      }
      if (tag == "sbr") {
        title = "SBR";
      }
      if (tag == "d") {
        title = "D";
      }
      // 
      const y_test = {
        x: Object.keys(this.train_algorithm_lr_result[tag].y_test),
        y: this.train_algorithm_lr_result[tag].y_test,
        type: 'lines+markers',
        name: ""
      };
      const y_pred = {
        x: Object.keys(this.train_algorithm_lr_result[tag].y_pred),
        y: this.train_algorithm_lr_result[tag].y_pred,
        type: 'lines+markers',
        name: ""
      };
      const data = [y_test, y_pred];
      const layout = {
        title
        // xaxis: {
        //   title: ''
        // }
      };
      Plotly.newPlot(id, data, layout);
    },
  },
  mounted() {
    // 
    if (Object.keys(this.train_algorithm_lr_result).length > 0) {
      this.draw_lineplot_all();
    }
  }
};
</script>

<style lang="scss" scoped>
#train-algorithm-lr {

  // card
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    .setting-method {
      margin-bottom: 20px;
    }
  }

  // .model-tool {
  //   display: flex;
  //   flex-direction: row;
  //   justify-content: space-between;
  //   align-items: center;
  // }
}
</style>
