<template>
  <div id="train-process-abnormal">
    <el-card shadow="hover">
      <div class="setting">
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-select v-model="train_process_setting.abnormal[0].state" placeholder="">
            <el-option v-for="state in process_abnormal_states" :key="state.value" :label="state.label"
              :value="state.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-select v-model="train_process_setting.abnormal[0].detect" placeholder="">
            <el-option v-for="detect in process_abnormal_detects" :key="detect.value" :label="detect.label"
              :value="detect.value">
            </el-option>
          </el-select>
        </div>
        <div class="setting-method" style="margin-right: 20px;">
          <span></span>
          <el-select v-model="train_process_setting.abnormal[0].method" placeholder="">
            <el-option v-for="method in process_abnormal_methods" :key="method.value" :label="method.label"
              :value="method.value">
            </el-option>
          </el-select>
        </div>
      </div>
    </el-card>
    <el-card class="info" shadow="hover">
      <div slot="header">
        <b>
          
        </b>
      </div>
      <el-row :gutter="12">
        <el-col :span="12">
          
        </el-col>
        <el-col :span="12">
          
        </el-col>
      </el-row>
    </el-card>
    <!-- <el-card class="condition" shadow="hover">
      <div slot="header">
        <b>
          
        </b>
      </div>
      <div id="train-process-boxplot">
        11
      </div>
    </el-card> -->
  </div>
</template>

<script>
import Plotly from 'plotly.js-dist-min';


export default {
  name: 'TrainProcessAbnormal',
  data() {
    return {
      // 
      process_abnormal_states: [
        {
          value: 0,
          label: ''
        }, {
          value: 1,
          label: ''
        }
      ],
      // 
      process_abnormal_detects: [
        {
          value: 1,
          label: 'IQR'
        }, {
          value: 2,
          label: 'Z-score'
        }
      ],
      // 
      process_abnormal_methods: [
        {
          value: 1,
          label: ''
        }, {
          value: 2,
          label: ''
        }, {
          value: 3,
          label: ''
        }, {
          value: 4,
          label: ''
        }, {
          value: 5,
          label: ''
        }
      ],
      // 
      columns: ["SPO", "BMPO", "BFPO", "SPE", "BMPE", "BFPE", "ST", "BT", "WP", "FN", "SFR", "BFR","BSR","SBR","D"],
    };
  },
  computed: {
    train_process_setting: { // 
      get() {
        return this.$store.state.train_process_setting;
      },
      set(newVal) {
        this.$store.commit('set_train_process_setting', newVal);
      }
    },
  },
  watch: {
    // train_process_setting
    train_process_setting: {
      handler(newVal, oldVal) {
        this.train_process_setting = newVal;
      },
      deep: true,
      immediate: true
    },
  },
  methods: {
    // 
    draw_boxplot() {
      // 
      let data_coloum = {};
      this.columns.map(column => data_coloum[column] = []);
      this.process_input_data.map(row_data => {
        for (let coloum in data_coloum) {
          if (typeof row_data[coloum] != 'undefined') {
            data_coloum[coloum].push(row_data[coloum]);
          }
        }
      });
      // 
      let data = [];
      this.columns.map(column => {
        data.push({
          y: data_coloum[column],
          type: 'box',
          name: column,
          marker: {
            color: 'rgb(8,81,156)',
            outliercolor: 'rgba(219, 64, 82, 0.6)',
            line: {
              outliercolor: 'rgba(219, 64, 82, 1.0)',
              outlierwidth: 2
            }
          },
          boxpoints: 'suspectedoutliers'
        });
      });
      const layout = {
        // title: ''
        autosize: true, // 
        showlegend: false, // 
      };
      Plotly.newPlot('train-process-boxplot', data, layout);
      // store
      this.process_boxplot = data;
    }
  },
  mounted() {
    // 
    //   this.draw_boxplot();
  }
};
</script>

<style lang="scss" scoped>
#train-process-abnormal {

  // card
  &>* {
    margin-bottom: 20px;
  }

  .setting {
    display: flex;
    flex-direction: row;
  }
}
</style>